/* Color palette */
[data-dark="false"] {
  --primary: #1a73e8;
  --secondary: #64b5f6;
  --text: #2c3e50;
  --background: #f5f5f5;
  --background-alt: #fafafa;
  --light-gray: #d3d3d3;
  --gray: #7f8c8d;
  --overlay: rgba(0, 0, 0, 0.2);
  }
  [data-dark="true"] {
  --primary: #1a73e8;
  --secondary: #64b5f6;
  --text: #f5f5f5;
  --background: #263238;
  --background-alt: #37474f;
  --light-gray: #7f8c8d;
  --gray: #d3d3d3;
  --overlay: rgba(255, 255, 255, 0.1);
  }
  
  

:root {
  // font families
  --title: "Barlow", sans-serif;
  --heading: "Barlow", sans-serif;
  --body: "Barlow", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --medium: 1rem;
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 2;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
}

